import { Component } from '@angular/core';
import { Platform } from '@ionic/angular';
import {
  TravelMarker,
  TravelMarkerOptions,
  TravelData,
  TravelEvents,
  EventType,
} from 'travel-marker';

import { HttpClient } from '@angular/common/http';

declare var google: any;

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  title = 'BTT - FishTracker';

  height = 0;
  zoom = 9;
  lat = 25;
  lng = -80.627836399999983;
  map: any;
  line: any;
  directionsService: any;
  marker: TravelMarker = null;
  speedMultiplier = 1;
  serviceURL = 'https://btt-api.herokuapp.com/tarpons?ID=';

  constructor(public platform: Platform, private http: HttpClient) {
    this.height = platform.height();
  }

  onMapReady(map: any) {
    this.map = map;
    this.loadLayerData('18574');
  }

  loadLayerData(layerId: string) {
    const locationArray: any[] = [];

    this.http
      .get(this.serviceURL + layerId)
      .subscribe(layerData => this.loadMap(layerData as any[]));
  }

  private loadMap(points: any[]) {
    this.createRoutePath(points);
    this.setStartEndPoints(points);
    this.initRoute();
  }

  private createRoutePath(points: any[]) {
    const locationArray: any[] = [];

    // I know this can be done via pipe(map())
    points.forEach(x =>
      locationArray.push(new google.maps.LatLng(x.Latitude, x.Longitude)),
    );

    this.line = new google.maps.Polyline({
      strokeOpacity: 0.1,
      path: [],
      map: this.map,
    });
    locationArray.forEach(l => this.line.getPath().push(l));
  }

  private setStartEndPoints(points: any[]) {
    const initialPoint = new google.maps.LatLng(
      points[0].Latitude,
      points[0].Longitude,
    );
    const endPoint = new google.maps.LatLng(
      points[points.length - 1].Latitude,
      points[points.length - 1].Longitude,
    );
    const startMarker = new google.maps.Marker({
      position: initialPoint,
      map: this.map,
      label: 'Initial Location',
    });
    const endMarker = new google.maps.Marker({
      position: endPoint,
      map: this.map,
      label: 'Last Location',
    });
  }

  private initRoute() {
    const route = this.line.getPath().j;

    // options
    const options: TravelMarkerOptions = {
      map: this.map, // map object
      speed: 1000, // default 10 , animation speed
      interval: 10, // default 10, marker refresh time
      speedMultiplier: this.speedMultiplier,
      markerOptions: {
        title: 'Travel Marker',
        animation: google.maps.Animation.DROP,
        icon: {
          url:
            'https://greyghostcharters.com/wp-content/uploads/2014/04/tarpon-species.png',
          // This marker is 20 pixels wide by 32 pixels high.
          animation: google.maps.Animation.DROP,
          scaledSize: new google.maps.Size(94, 47),
          origin: new google.maps.Point(0, 0),
          rotation: 33.25,
          anchor: new google.maps.Point(10, 5),
        },
      },
    };

    // define marker
    this.marker = new TravelMarker(options);

    // add locations from direction service
    this.marker.addLocation(route);

    setTimeout(() => this.play(), 2000);
  }

  play() {
    this.marker.play();
  }

  pause() {
    this.marker.pause();
  }

  reset() {
    this.marker.reset();
  }

  next() {
    this.marker.next();
  }

  prev() {
    this.marker.prev();
  }

  fast() {
    this.speedMultiplier *= 2;
    this.marker.setSpeedMultiplier(this.speedMultiplier);
  }

  slow() {
    this.speedMultiplier /= 2;
    this.marker.setSpeedMultiplier(this.speedMultiplier);
  }

  initEvents() {
    if (this.marker) {
      this.marker.event.onEvent((event: EventType, data: TravelData) => {
        console.log(event, data);
      });
    }
  }
}
